import { Box, Button, Chip, Typography } from "@mui/material";
import React from "react";
import { useNavigate } from "react-router-dom";
import CodeIcon from '@mui/icons-material/Code';
import PlaceIcon from '@mui/icons-material/Place';
import SchoolIcon from '@mui/icons-material/School';
import DataObjectIcon from '@mui/icons-material/DataObject';
import LanguageIcon from '@mui/icons-material/Language';
import EmojiPeopleIcon from '@mui/icons-material/EmojiPeople';


const Start = () => {
    // eslint-disable-next-line
    const history = useNavigate();

    return (
        <Box component="main">
            <Box className="flex-column">
                <Typography variant="h3">
                    Богатырев Никита
                </Typography>
                <Box className="flex-column gap-0 wrap">
                    <Typography variant="h5" className="note">
                        Junior Fullstack программист
                    </Typography>
                    <Box className="flex-row gap-0 wrap">
                        <CodeIcon className="note" />
                        <Chip label="JavaScript" className="bg-js" />
                        <Chip label="TypeScript" className="bg-ts" />
                        <Chip label="NodeJS" className="bg-node" />
                        <Chip label="React" className="bg-react" />
                    </Box>
                </Box>
                <Box className="flex-row gap-0 wrap" sx={{ mt: 1 }}>
                    <PlaceIcon className="note" />
                    <Typography variant="body1" className="note">
                        Россия, Москва
                    </Typography>
                </Box>
            </Box>
            <Box className="flex-column gap-1" sx={{ mt: 5 }}>
                <Typography variant="h4" className="title-color">
                    Коротко обо мне:
                </Typography>
                <Box className="flex-column gap-0">
                    <Typography variant="body1">
                        &nbsp;-&nbsp;Учусь на первом курсе колледжа по специальности программист
                    </Typography>
                    <Typography variant="body1">
                        &nbsp;-&nbsp;Работаю в веб направлении, как frontend, так и backend developer
                    </Typography>
                    <Typography variant="body1">
                        &nbsp;-&nbsp;Опыт работы над проектами(как личными, так и нет)
                    </Typography>
                    <Typography variant="body1">
                        &nbsp;-&nbsp;Опыт работы в компании отсутствует
                    </Typography>
                </Box>
            </Box>
            <Box className="flex-column gap-1" sx={{ mt: 5 }}>
                <Typography variant="h4" className="title-color">
                    Проекты:
                </Typography>
                <Box className="flex-column gap-0">
                    <Box className="flex-row wrap gap-0">
                        <Button 
                            variant="contained" 
                            className="button-style"
                            onClick={() => window.open(`https://roso1nik.github.io/truth-or-dare/`, "_blank")}
                        >
                            Правда или действие?
                        </Button>
                        <Typography variant="h6">&nbsp;-&nbsp;Игра "Правда или действие" с новыми фишками</Typography>
                    </Box>
                    <Box className="flex-row wrap gap-0">
                        <Button 
                            variant="contained" 
                            className="button-style"
                            onClick={() => window.open(`https://roso1nik.github.io/shaverma-tour/`, "_blank")}
                        >
                            Shaverma Tour
                        </Button>
                        <Typography variant="h6">&nbsp;-&nbsp;Проект придуманный в КС54</Typography>
                    </Box>
                    <Box className="flex-row wrap gap-0">
                        <Button 
                            variant="contained" 
                            className="button-style"
                            onClick={() => window.open(`https://roso1nik.github.io/`, "_blank")}
                        >
                            Taplink
                        </Button>
                        <Typography variant="h6">&nbsp;-&nbsp;Taplink и другие проекты</Typography>
                    </Box>
                </Box>
            </Box>
            <Box className="flex-column gap-1" sx={{ mt: 5 }}>
                <Typography variant="h4" className="title-color">
                    Образование и умения
                </Typography>
                <Box className="flex-column gap-0">
                    <Box className="flex-row gap-1">
                        <SchoolIcon className="note" />
                        <Typography variant="body1">
                            Колледж Связи №54 по специальности программист
                        </Typography>
                    </Box>
                    <Box className="flex-row gap-1">
                        <DataObjectIcon className="note" />
                        <Typography variant="body1">
                            JS, TS, React, python, nodejs, express, mongodb, nosql, html/css
                        </Typography>
                    </Box>
                    <Box className="flex-row gap-1">
                        <EmojiPeopleIcon className="note" />
                        <Typography variant="body1">
                            Отношусь к работе ответсвенно и с душой, коммуникабелен и стрессоустойчив
                        </Typography>
                    </Box>
                    <Box className="flex-row gap-1">
                        <LanguageIcon className="note" />
                        <Typography variant="body1">
                            Знаю английский на уровне B2
                        </Typography>
                    </Box>
                </Box>
            </Box>
            <Box className="flex-column gap-1" sx={{ mt: 5 }}>
                <Typography variant="h4" className="title-color">
                    Дополнительная информация:
                </Typography>
                <Box className="flex-column gap-0">
                    <Typography variant="body1">
                        Готов работать как в офисе, так и удалено
                    </Typography>
                </Box>
            </Box>
        </Box>
    )
}

export default Start;